.choose {
	position: relative;
	// min-height: 100vh;
	&__wrapper {
		display: flex;
		gap: clamp(1.875rem, 0.996rem + 4.4vw, 4.375rem);
		height: 900px;
		@media (max-width: 850px) {
			display: flex;
			flex-direction: column;
			height: 1300px;
		}
	}

	&__left {
		position: relative;
		width: 50%;
		background-image: url("../images/choose/choose-image.jpg");
		background-position: top;
		background-repeat: no-repeat;
		background-size: cover;
		display: flex;
		justify-content: center;
		align-items: center;
		// height: 100vh;
		padding: 30px;
		@media (max-width: 850px) {
			width: 100%;
			height: 100%;
		}
	}

	&__stats {
		display: flex;
		flex-direction: column;
		justify-content: center;
		text-align: center;
		align-items: center;
		width: 100%;
		max-width: 560px;
		// height: auto;
		background-color: rgba(28, 39, 82, 0.86);
		gap: clamp(0.625rem, 0.295rem + 1.65vw, 1.563rem);
		padding: clamp(3.125rem, 2.026rem + 5.49vw, 6.25rem) 25px;
	}

	&__stat {
		display: flex;
		align-items: center;
		gap: 20px;
		border: 1px solid white;
		font-family: "Mulish", sans-serif;
		width: 100%;
		max-width: 400px;
		height: clamp(2.188rem, 1.484rem + 3.52vw, 4.188rem);
		gap: 20px;
		padding-right: 10px;

		p {
			color: white;
			font-size: clamp(0.813rem, 0.593rem + 1.1vw, 1.438rem);
		}
	}

	&__title {
		background-color: rgba(255, 179, 66, 1);
		max-width: clamp(5rem, 4.341rem + 3.3vw, 6.875rem);
		padding: 0 5px;
		width: 100%;
		text-align: center;
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
		h2 {
			font-size: clamp(0.938rem, 0.74rem + 0.99vw, 1.5rem);
			color: #1c2752;
			line-height: 35px;
		}
	}

	// Right

	&__right {
		position: relative;
		width: 590px;
		height: auto;
		// height: 692px;
		padding: 0px 15px 45px 0px;
		display: flex;
		justify-content: center;
		align-items: center;
		// height: 100vh;
		@media (max-width: 850px) {
			width: 100%;
			height: 100%;
			padding: 0 25px;
		}
	}

	&__above {
		display: flex;
		align-items: center;
		background-color: #f4f4f4;
		max-width: 250px;
		gap: 10px;
		color: #1c2752;
		// @include title-above;

		span {
			@include title-above-icon;
			padding-left: 20px;
		}
	}

	&__wrap {
		h1 {
			@include title;
			@media (max-width: 400px) {
				// text-align: center;
			}
		}
	}

	&__descript {
		@include descript;
		@media (max-width: 600px) {
			text-align: left;
			line-height: clamp(1.25rem, 1.096rem + 0.77vw, 1.688rem);
		}
	}

	&__tables {
		max-width: 500px;
		margin-top: 20px;
	}

	&__table {
		max-width: 480px;

		p {
			padding-left: clamp(2.813rem, 2.497rem + 1.58vw, 3.75rem);
			padding-right: 10px;
			@include descript;
			@media (max-width: 600px) {
				// text-align: left;
				line-height: clamp(1.25rem, 1.096rem + 0.77vw, 1.688rem);
			}
		}
	}

	&__abovet {
		display: flex;
		align-items: center;
		gap: 20px;
		span {
			background-color: #ffb342;
			padding: clamp(0.313rem, 0.203rem + 0.55vw, 0.625rem);
			border-radius: 200px;
			font-size: clamp(0.875rem, 0.787rem + 0.44vw, 1.125rem);
			font-weight: 600;
		}
		h1 {
			font-size: clamp(0.938rem, 0.828rem + 0.55vw, 1.25rem);
		}
	}
}
