.services {
	background-color: #f4f4f4;
	&__wrapper {
		text-align: center;
		padding-top: clamp(5rem, 3.681rem + 6.59vw, 8.75rem);

		padding-top: 150px;
		padding-bottom: 140px;
	}

	&__above {
		background-color: white;
	}

	&__descript {
		max-width: 819px;
		height: 54px;
		color: #7e7e7e;
	}

	&__blocks {
		margin-top: 40px;
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 30px;
		.block {
			background-color: #1c2752;
			padding: clamp(1.563rem, 1.013rem + 2.75vw, 3.125rem) clamp(0.938rem, 0.718rem + 1.1vw, 1.563rem)
				clamp(1.563rem, 1.013rem + 2.75vw, 3.125rem) clamp(1.563rem, 1.343rem + 1.1vw, 2.188rem);
			display: flex;
			flex-direction: column;
			justify-content: center;
			width: min(100%, 32rem);
			text-align: left;
			i {
				font-size: clamp(1.563rem, 1.123rem + 2.2vw, 2.813rem);
				color: #ffb342;
			}
			h1 {
				color: #ffb342;
				//20=12
				margin: clamp(0.75rem, 0.574rem + 0.88vw, 1.25rem) 0 clamp(0.5rem, 0.346rem + 0.77vw, 0.938rem) 0;
				//25 = 15
				font-size: clamp(0.938rem, 0.718rem + 1.1vw, 1.563rem);
				line-height: clamp(1.25rem, 0.876rem + 1.87vw, 2.313rem);
			}
			p {
				//17 = 12
				font-size: clamp(0.75rem, 0.64rem + 0.55vw, 1.063rem);
				//28 = 15
				line-height: clamp(0.938rem, 0.652rem + 1.43vw, 1.75rem);
				color: #d2d2d2;
				max-width: 320px;
				letter-spacing: 0.36px;
				margin-bottom: 22px;
			}

			a {
				color: white;
				font-size: 18px;
			}
		}
	}

	// btn

	&__button {
		border: 2px solid #1c2752;
		width: 150px;
		height: 40px;
		color: #1c2752;
		transition: all 0.3s ease-in-out;
		display: none;
		margin: 0 auto;
		margin-top: 30px;
		&:hover {
			background-color: #ffb342;
			border: 2px solid #ffb342;

			color: white;
			// border: none;
		}
	}
}

@media (max-width: 900px) {
	.services {
		&__blocks {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			.open {
				display: block;
			}
		}

		.block {
			display: none;
			opacity: 0; // Початково непрозорі для анімації
			transform: translateY(-20px); // Трохи піднімаємо блок для ефекту
			&.visible {
				display: block;
				opacity: 1;
				transform: translateY(0px);
				transition: opacity 0.3s ease, transform 0.3s ease;
			}

			// Щоб приховати з анімацією
			&.hide {
				opacity: 0;
				transform: translateY(-20px);
				transition: opacity 0.5s ease, transform 0.5s ease;
			}
		}

		&__button {
			display: block;
		}
	}
}

@media (max-width: 500px) {
	.services {
		&__blocks {
			// margin-top: 40px;
			display: grid;
			grid-template-columns: repeat(1, 1fr);
			// gap: 30px;
		}
	}
}
