.update {
	padding-top: clamp(5rem, 3.526rem + 7.37vw, 9.375rem);
	&__title {
		margin-bottom: 40px;
		color: #1c2752;
	}

	&__blocks {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 30px;
		@media (max-width: 600px) {
			grid-template-columns: repeat(1, 1fr);
			gap: 0;
		}
	}

	&__block {
		margin-bottom: 140px;
		@media (max-width: 600px) {
			grid-template-columns: repeat(1, 1fr);
			margin-bottom: 60px;
		}
		img {
			position: relative;
			width: 100%;
			width: 580px;
		}

		h5 {
			@include title;
			color: #1c2752;
			margin-bottom: 15px;
			font-size: clamp(1.063rem, 0.789rem + 1.37vw, 1.875rem);
			line-height: clamp(1.563rem, 1.205rem + 1.79vw, 2.625rem);
		}

		p {
			@include descript;
			color: #7e7e7e;
			margin-bottom: 17px;
			line-height: clamp(0.938rem, 0.685rem + 1.26vw, 1.688rem);
		}
	}

	&__img {
		position: relative;
		margin-bottom: 50px;
	}

	&__decorations {
		display: flex;
		position: absolute;
		left: 50%;
		bottom: -25px;
		transform: translate(-50%);
		width: 100%; // Робимо контейнер на всю ширину
		justify-content: center;
		.decoration__one,
		.decoration__two {
			flex: 1 1 clamp(7.5rem, 6.237rem + 6.32vw, 11.25rem); // Фіксована базова ширина 180px з можливістю автоматично зменшуватися
			max-width: clamp(7.5rem, 6.237rem + 6.32vw, 11.25rem); // Максимальна ширина 180px
			display: flex;
			justify-content: center;
			align-items: center;
			// padding: 10px;
			text-align: center;
		}

		.decoration__one {
			background: #ffc368;
		}

		.decoration__two {
			background: #ffb342;
		}
	}

	&__decoration {
		display: flex;
		align-items: center;
		gap: 10px;
		width: 100%;
		max-width: clamp(7.5rem, 6.237rem + 6.32vw, 11.25rem);
		height: clamp(1.875rem, 1.201rem + 3.37vw, 3.875rem);

		i,
		p {
			padding: 0;
			margin: 0;
			color: #1c2752;
			font-family: Inter, sans-serif;
			font-size: clamp(0.75rem, 0.687rem + 0.32vw, 0.938rem);
		}

		i {
			font-size: clamp(1.063rem, 0.999rem + 0.32vw, 1.25rem);
			font-weight: bold;
		}
	}

	&__btn {
		font-family: Inter, sans-serif;
		display: flex;
		align-items: center;
		font-size: clamp(0.938rem, 0.832rem + 0.53vw, 1.25rem);
		color: #1c2752;
		i {
			font-size: clamp(0.938rem, 0.832rem + 0.53vw, 1.25rem);
		}
	}
}
