.offer {
	height: 900px;
	background-color: #ffb342;
	padding-top: clamp(6.25rem, 5.371rem + 4.4vw, 8.75rem);
	// margin-bottom: 100px;

	&__above {
		display: flex;
		align-items: center;
		background: #f4f4f4;
		justify-content: center;
		max-width: 200px;
		width: 100%;
		gap: 10px;
		margin: 0 auto;
		margin-bottom: 22px;
		i {
			@include title-above-icon;
		}

		p {
			@include title-above;
		}
	}

	&__title {
		@include title;
		display: flex;
		justify-content: center;
		text-align: center;
	}

	&__descript {
		@include descript;
		max-width: 832px;
		display: flex;
		justify-content: center;
		text-align: center;
		margin: 0 auto;
		color: #1c2752;
		margin-bottom: 40px;
		@media (max-width: 600px) {
			line-height: clamp(1.25rem, 1.096rem + 0.77vw, 1.688rem);
		}
	}
}

.tabs {
	&__btns {
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 30px;
		@media (max-width: 600px) {
			flex-direction: column;
			gap: 10px;
		}
	}

	.btn {
		display: flex;
		align-items: center;
		background-color: #f4f4f4;
		width: 100%;
		max-width: 249px;
		height: 68px;
		gap: 10px;
		padding: 0 15px;
		justify-content: center;
		font-family: Inter, sans-serif;
		//20 = 15
		font-size: clamp(0.938rem, 0.828rem + 0.55vw, 1.25rem);
		line-height: 30px;
		color: #222;
		cursor: pointer;
		transition: all 0.2s ease-in-out;
		&.active {
			background-color: #ffffff;
			transform: translateY(-8px);
		}

		@media (max-width: 600px) {
			&.active {
				background-color: #ffffff;
				transform: translateY(0px);
			}
		}

		i {
			@include title-above-icon;
			color: #1c2752;
			font-weight: normal;
		}
	}
}

.tabs__contents {
	position: relative;
	background-color: white;
	max-width: 1160px;
	margin-top: 40px;
	padding: 60px;
	@media (max-width: 600px) {
		padding: 30px;
	}
}

.content {
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 50px;
	display: none;
	&.active {
		display: flex;
	}

	&__left {
		max-width: 50%;
		@media (max-width: 800px) {
			max-width: 100%;
		}
	}

	&__right {
		max-width: 50%;
		@media (max-width: 800px) {
			display: none;
		}
	}

	&__above {
		display: flex;
		align-items: center;
		max-width: 259px;
		background-color: #f4f4f4;
		text-align: center;
		justify-content: center;
		gap: 10px;
		margin-bottom: 22px;
		i {
			@include title-above-icon;
		}
		p {
			@include title-above;
		}
	}

	&__title {
		@include title;
		margin-bottom: 12px;
	}

	&__descript {
		@include descript;
		padding-bottom: 10px;
		@media (max-width: 600px) {
			line-height: clamp(1.25rem, 1.096rem + 0.77vw, 1.688rem);
		}
	}

	&__btn {
		display: flex;
		justify-content: center;
		text-align: center;
		align-items: center;
		margin-top: 17px;
		max-width: clamp(7.5rem, 5.962rem + 7.69vw, 11.875rem);

		height: clamp(2.5rem, 1.885rem + 3.08vw, 4.25rem);
		border: 2px solid #ffb342;
		background: #ffb342;
		color: #1c2752;
		font-family: Inter, sans-serif;
		font-size: clamp(0.938rem, 0.828rem + 0.55vw, 1.25rem);
		cursor: pointer;
	}
}
