.header {
	background-color: #f2f2f2;
	&__container {
		max-width: 1230px;
		margin: 0 auto;
		padding: 0 25px;
		width: 100%;
	}

	&__wrapper {
		width: 100%;
	}

	&__above {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		height: 80px;
	}

	&__schedule {
		@include title-above;
	}

	&__social {
		display: flex;
		gap: clamp(0.938rem, 0.824rem + 0.57vw, 1.25rem);
		color: #1c2752;
		i {
			font-size: 25px;
		}
	}

	//

	&__information {
		background-color: white;
	}

	&__conversation {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 30px 15px;
	}

	&__logo {
		background-repeat: no-repeat;
		width: clamp(9.375rem, 8.239rem + 5.68vw, 12.5rem);
		height: 40px;
		display: flex;
		margin-right: 10px;
		text-align: center;
		justify-content: center;
		text-align: center;
		align-items: center;
		a {
			color: white;
			font-weight: 600;
			letter-spacing: 10px;
			font-family: sans-serif;
		}
	}

	&__communicate {
		a {
			background-color: #ffb342;
			display: flex;
			padding: clamp(0.5rem, 0.346rem + 0.77vw, 0.938rem);
			border-radius: 500px;
			font-size: clamp(0.938rem, 0.718rem + 1.1vw, 1.563rem);
			color: #1c2752;
		}
		ul {
			display: flex;
			gap: clamp(1.563rem, 1.222rem + 1.7vw, 2.5rem);
			align-items: center;
		}

		li {
			position: relative;
			display: flex;
			align-items: center;
			gap: 10px;
			cursor: pointer;
			padding: 5px 5px 8px 5px;

			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				border-bottom: 1px solid transparent; /* Початково невидимий */
				transition: border-color 0.3s ease; /* Плавний перехід кольору межі */
			}

			&:hover::before {
				border-color: rgba(50, 50, 93, 0.35); /* Показує межу при ховері */
			}
		}
	}

	&__mail,
	&__call {
		h1 {
			font-size: clamp(1.063rem, 0.994rem + 0.34vw, 1.25rem);
			color: #1c2752;
		}

		p {
			color: #7e7e7e;
			font-size: clamp(0.75rem, 0.64rem + 0.55vw, 1.063rem);
		}
	}

	//menu
	&__right {
		display: flex;
		align-items: center;
		gap: 15px;
	}
}

@media (max-width: 660px) {
	.header {
		&__conversation {
			display: flex;
			flex-direction: column;
		}

		.menu-btn {
			display: flex;
			flex-direction: column;
			gap: 6px;
			div {
				width: 30px;
				height: 3px;
				background-color: #1c2752;
			}
		}
	}
}

.menu-btn {
	display: none;
	div {
		width: 30px;
		height: 3px;
		background-color: #1c2752;
	}
}

@media (max-width: 500px) {
	.header {
		&__above {
			display: flex;
			flex-direction: column;
			align-items: center;
			gap: 10px;
			height: 85px;
			padding: 5px;
		}

		&__schedule {
			display: flex;
			align-items: center;
			justify-content: center;
			background-color: #e18c0c;
			width: 100%;
			color: white;
			padding: 5px 0;
		}

		&__right {
			display: flex;
			justify-content: space-around;
			width: 100%;
		}

		// mennu

		.menu {
			position: fixed;
			width: 75%;
		}
	}
}

@media (max-width: 365px) {
	.header {
		&__communicate a {
			display: none;
		}
	}
}
