.plan {
	background-color: #f4f4f4;
	padding-top: clamp(5rem, 3.526rem + 7.37vw, 9.375rem);

	&__block {
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 50px;
		@media (max-width: 625px) {
			text-align: left;
			flex-direction: column;
			gap: 10px;
		}
	}

	&__above {
		display: flex;
		align-items: center;
		gap: 10px;
		background-color: white;
		max-width: 160px;
		justify-content: center;

		i {
			@include title-above-icon;
		}

		p {
			@include title-above;
		}
	}

	&__left {
		display: flex;
		flex-direction: column;
		@media (max-width: 625px) {
			text-align: left;
			flex-direction: column;
			gap: 10px;
		}
	}

	&__title {
		color: #1c2752;
		max-width: 497px;
		width: 100%;
		@include title;
	}

	&__right {
		p {
			max-width: 585px;
			color: #7e7e7e;
			@include descript;
		}
	}
}

// columns

.columns {
	margin-top: 60px;
	padding-bottom: 60px;
	&__blocks {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 30px;
		@media (max-width: 1035px) {
			grid-template-columns: repeat(2, 1fr);
			justify-items: center;
		}

		@media (max-width: 625px) {
			grid-template-columns: repeat(1, 1fr);
			justify-items: center;
		}
	}

	&__block {
		width: 100%;
		max-width: 380px;
		background-color: white;
		padding: 40px 40px 0px 40px;
		transition: all 0.3s ease-in-out;
		border-radius: 3px;
		cursor: pointer;
		@media (max-width: 1035px) {
			padding: 40px 40px 0px 40px;
		}

		@media (max-width: 625px) {
			max-width: 350px;
		}
		&:hover {
			box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
		}
		.line {
			height: 1px;
			width: 100%;
			background-color: #d7d7d7;
		}
	}

	&__above {
		display: flex;
		justify-content: space-between;
		align-items: end;
		gap: 10px;
		padding-bottom: 30px;
	}

	&__left {
		display: flex;
		flex-direction: column;
		gap: 7px;
		font-family: Inter, sans-serif;
		font-size: clamp(0.938rem, 0.832rem + 0.53vw, 1.25rem);
		font-weight: 400;
		text-align: left;
		color: #1c2752;

		span {
			font-family: Mulish, sans-serif;
			font-weight: 400;
			text-align: left;
			font-size: clamp(1.25rem, 0.997rem + 1.26vw, 2rem);
		}
	}

	&__right {
		font-family: Inter, sans-serif;
		font-size: clamp(0.625rem, 0.52rem + 0.53vw, 0.938rem);
		font-weight: 400;
		text-align: left;
		display: flex;
		flex-direction: column;
		gap: 15px;
		p {
			color: #7e7e7e;
		}
	}

	.popular {
		background-color: #ffb342;
		padding: 2px 5px;
		display: flex;
		align-items: center;
		text-align: center;
		justify-content: center;
		text-transform: uppercase;
		font-family: Inter, sans-serif;
		font-size: clamp(0.75rem, 0.666rem + 0.42vw, 1rem);
		text-align: left;
		color: #1c2752;
	}

	//

	&__btn {
		display: flex;
		justify-content: center;
		margin-top: 55px;
		text-align: center;
		background: #ffb342;
		height: clamp(2.813rem, 2.497rem + 1.58vw, 3.75rem);
		margin-bottom: 60px;
		p {
			font-family: Inter, sans-serif;
			font-size: clamp(1.063rem, 0.999rem + 0.32vw, 1.25rem);
			display: flex;
			text-align: center;
			justify-content: center;
			align-items: center;
			color: #1c2752;
		}
	}
}

// solution

.solution {
	margin-top: 30px;

	&__blocks {
		display: flex;
		flex-direction: column;
		gap: 20px;
	}

	&__block {
		display: flex;
		align-items: center;
		gap: clamp(0.938rem, 0.832rem + 0.53vw, 1.25rem);
		i {
			width: 19px;
			height: 19px;
			color: #ffb342;
		}

		p {
			@include descript;
			@media (max-width: 600px) {
				line-height: clamp(1.25rem, 1.096rem + 0.77vw, 1.688rem);
			}
		}
	}
}

// below

.below {
	&__blocks {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		align-items: center;
		gap: clamp(2.813rem, 2.286rem + 2.63vw, 4.375rem);
		@media (max-width: 680px) {
			display: flex;
			flex-direction: column;
			padding: 0;
			margin: 0;
			gap: 0px;
		}
	}

	&__block {
		display: flex;
		gap: 15px;
		margin-bottom: 120px;
		@media (max-width: 680px) {
			// padding: 0;
			margin-bottom: 30px;
			// gap: 0px;
			line-height: clamp(1.25rem, 1.096rem + 0.77vw, 1.688rem);
		}

		h5 {
			font-family: Mulish, sans-serif;
			font-size: 20px;
			text-align: left;
			color: #1c2752;
		}

		p {
			@include descript;
		}

		i {
			display: block;
			padding: 20px;
			background-color: #ffb342;
		}
	}

	&__left,
	&__right {
		display: flex;
		flex-direction: column;
		gap: 10px;
	}
}
