.gallery {
	background-color: #1c2752;
	&__wrapper {
		padding-top: 140px;
		padding-bottom: 200px;
	}

	&__above {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
		margin: 0 auto;
		background-color: #f4f4f4;
		width: 180px;
		margin-bottom: 22px;
		i {
			@include title-above-icon;
		}
		p {
			@include title-above;
		}
	}

	&__title {
		@include title;
		color: white;
		text-align: center;
	}

	// ======================

	&__blocks {
		display: grid;
		gap: 30px;
		max-width: 1400px;
		width: 100%;
		margin: 50px auto 0 auto;
		padding: 0 20px;
		grid-template-columns: repeat(4, 1fr);
		@media (max-width: 1200px) {
			grid-template-columns: repeat(3, 1fr);
			justify-items: center;
		}

		@media (max-width: 850px) {
			grid-template-columns: repeat(2, 1fr);
			justify-content: center;
			justify-items: center;
		}

		@media (max-width: 600px) {
			grid-template-columns: repeat(1, 1fr);
			justify-content: center;
			justify-items: center;
		}
	}

	&__block {
		background-color: #f4f4f4;
		text-align: center;
		max-width: 320px;
		padding-bottom: 25px;

		img {
			width: 100%;
			object-fit: cover;
			padding: 20px 20px 0 20px;
		}
		h1 {
			padding-top: 25px;
			font-size: clamp(0.938rem, 0.727rem + 1.05vw, 1.563rem);
			// width: 240px;
			margin: 0 auto;
			font-weight: 400;
		}

		p {
			line-height: #7e7e7e;
			font-size: 17px;
		}
	}
}
