.main {
	background-image: url("../images/main/bacckground.jpg");
	background-size: cover; /* Картинка покриває весь блок, зберігаючи пропорції */
	background-repeat: no-repeat;
	background-position: center; /* Вирівнювання по центру */
	padding: 260px 0 220px 0;
	position: relative;
	&__wrapper {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	&__block {
		position: relative;
		color: white;
		max-width: 720px;
		height: 330px;
	}

	&__above {
		display: flex;
		align-items: center;
		background-color: rgba(255, 255, 255, 0.2);
		max-width: 662px;
		gap: 10px;
		p {
			@include title-above;
			color: white;
		}
	}

	&__icon {
		@include title-above;
		@include title-above-icon;
		padding-left: 20px;
	}

	&__title {
		//60
		font-size: clamp(1.563rem, 0.793rem + 3.85vw, 3.75rem);
		font-family: "Mulish", sans-serif;
		line-height: clamp(2.5rem, 1.665rem + 4.18vw, 4.875rem);
		letter-spacing: 1.2px;
	}

	&__button {
		margin-top: clamp(1.25rem, 1.03rem + 1.1vw, 1.875rem);
		//227
		width: clamp(9.375rem, 7.683rem + 8.46vw, 14.188rem);
		text-align: center;
		font-size: clamp(1.063rem, 0.997rem + 0.33vw, 1.25rem);
		padding: clamp(0.625rem, 0.405rem + 1.1vw, 1.25rem) 0;
		background-color: #ffb342;
		a {
			color: #222;
		}
	}
}

.menu {
	position: absolute;
	top: 190px;
	width: 100%;
	z-index: 99;
	&__wrapper {
		display: flex;
		align-items: center;
		justify-content: space-between;
		background-color: #ffb342;
		// margin-top: 10px;
		//45
		padding: 0 10px 0 clamp(1.563rem, 1.108rem + 2.27vw, 2.813rem);
	}

	&__list {
		display: flex;
		align-items: center;
		//50
		gap: clamp(0.938rem, 0.142rem + 3.98vw, 3.125rem);
		padding-right: 30px;
	}

	li a {
		//20
		// font-size: clamp(1.125rem, 1.08rem + 0.23vw, 1.25rem);
		font-size: clamp(0.938rem, 0.828rem + 0.55vw, 1.25rem);
		font-weight: 400;
		color: #222;
	}

	&__btn {
		background-color: #1c2752;
		margin: 10px 0px 10px 0;
		padding: 10px;
		display: flex;
		justify-content: center;
		text-align: center;
		align-items: center;
		height: 68px;
		width: 300px;
		color: #f4f4f4;
		//20
		font-size: clamp(0.813rem, 0.653rem + 0.8vw, 1.25rem);
		font-weight: 400;
	}
}

@media (max-width: 660px) {
	.menu {
		position: fixed;
		top: 0;
		left: 0;
		height: 100vh;
		width: 50%;
		background-color: #1c2752;
		z-index: 1000;
		transform: translateX(-100%); // Початковий зсув за межі екрана
		transition: transform 0.3s ease-in-out; // Анімація зміни позиції

		&.active {
			box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
			transform: translateX(0); // Повертає меню на своє місце при активації
		}
		&__list {
			display: flex;
			z-index: 999;
			flex-direction: column;
			gap: clamp(1.875rem, 1.435rem + 2.2vw, 3.125rem);

			a {
				color: white;
			}
		}

		li a {
			color: white;
			letter-spacing: clamp(0.313rem, 0.203rem + 0.55vw, 0.625rem);
		}
		&__wrapper {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			background-color: transparent;
		}
		&__btn {
			display: none;
		}
	}
}
