.subscribe {
	background-color: #ffb342;
	padding-top: clamp(5rem, 3.737rem + 6.32vw, 8.75rem);
	padding-bottom: 50px;
	&__blocks {
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 50px;
		@media (max-width: 900px) {
			flex-direction: column;
		}
	}

	&__left {
		max-width: 410px;
		h5 {
			@include title;
			font-size: 32px;
		}

		p {
			@include descript;
			color: #1c2752;
		}
	}

	.email-input {
		border: 2px solid white;
		padding: 25px;
		font-size: clamp(0.75rem, 0.666rem + 0.42vw, 1rem);
		border-radius: 4px 0 0 4px;
		outline: none;
		height: clamp(1.875rem, 1.243rem + 3.16vw, 3.75rem);
		//350
		max-width: 350px;
		width: 100%;
	}

	input {
		width: 100%; // Поле вводу займає всю доступну ширину контейнера
		max-width: 300px; // Максимальна ширина
		// min-width: 150px; // Мінімальна ширина, щоб поле не ставало занадто малим
		color: white;

		&::placeholder {
			color: white;
			opacity: 0.5; // Напівпрозорий текст
		}

		&:focus::placeholder {
			color: white;
			opacity: 1; // Повна непрозорість при фокусі на полі
		}
	}

	.subscription-container {
		display: flex;
	}

	.subscribe-btn {
		background-color: white;
		color: #fca646;
		border: none;
		// padding: 20px 50px;
		font-size: clamp(0.813rem, 0.728rem + 0.42vw, 1.063rem);
		cursor: pointer;
		// height: 100%;
		color: #1c2752;
		border-radius: 0 4px 4px 0;
		outline: none;
		transition: background-color 0.3s ease;
		width: 181px;
	}

	.subscribe-btn:hover {
		background-color: #f0f0f0;
	}
}
