.about {
	padding: 100px 0;
	&__blocks {
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 30px;

		@media (max-width: 1050px) {
			flex-direction: column;
			gap: 20px;
		}
	}

	&__left {
		background-image: url("../images/about/about-picture.jpg");
		width: 100%;
		max-width: 600px;
		height: 100%;
		// height: 500px;
		aspect-ratio: 675 / 680;
		background-size: cover;
		background-position: center;
		@media (max-width: 1050px) {
			margin-top: -40px;
			aspect-ratio: 900 / 600;
			// max-width: 500px;
			height: 100%;
			border: none;
		}
	}

	&__icon {
		@include title-above-icon;
		padding-left: 20px;
		background-color: #f4f4f4;
	}

	&__above {
		display: flex;
		align-items: center;
		background-color: #f4f4f4;
		max-width: 250px;
		gap: 10px;

		i {
			@include title-above-icon;
		}

		p {
			// font-size: clamp(0.813rem, 0.703rem + 0.55vw, 1.125rem);
			@include title-above;
			color: #1c2752;
		}

		@media (max-width: 1050px) {
			width: 100%;
			gap: 5px;
		}
	}

	&__right {
		max-width: 575px;
		object-fit: cover;
		letter-spacing: 0.35px;

		h1 {
			@include title;
		}

		@media (max-width: 1050px) {
			max-width: 700px;
		}
	}

	&__descript {
		@include descript;
		padding-bottom: 10px;
		@media (max-width: 600px) {
			line-height: clamp(1.25rem, 1.096rem + 0.77vw, 1.688rem);
		}
	}

	&__experience {
		display: flex;
		gap: clamp(1.563rem, 1.233rem + 1.65vw, 2.5rem);
		align-items: center;
		margin-top: clamp(0.875rem, 0.633rem + 1.21vw, 1.563rem);

		@media (max-width: 1050px) {
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
			text-align: center;
		}

		.years {
			background-color: #f4f4f4;
			padding: 25px 50px 40px 50px;
			text-align: center;
			max-width: 200px;
			width: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			font-family: "Mulish", sans-serif;
			span {
				font-size: clamp(1.875rem, 0.776rem + 5.49vw, 5rem);
				line-height: clamp(1.875rem, 0.776rem + 5.49vw, 5rem);
				color: #ffb342;
				padding-bottom: 10px;
			}

			p {
				font-size: clamp(1.063rem, 0.997rem + 0.33vw, 1.25rem);
				color: #1c2752;
				line-height: 26px;
			}
		}

		.list {
			font-family: "Inter", sans-serif;
			display: flex;
			flex-direction: column;
			gap: 10px;
			font-size: clamp(0.813rem, 0.725rem + 0.44vw, 1.063rem);
			color: #7e7e7e;
		}
	}
}

@media (max-width: 530px) {
	.about {
		&__experience {
			display: flex;
			flex-direction: column;
			// gap: 10px;
		}
		.years {
			display: flex;
			flex-direction: row;
			align-items: center;
			max-width: 100%;
			gap: 10px;
			// margin-top: -5px;
			// margin-bottom: -25px;
			padding: 10px 10px;
			span {
				line-height: 0;
				font-size: 35px;
				padding-top: 10px;
			}
		}
	}

	.list {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		margin-top: -5px;
		font-size: 13px;

		li {
			display: flex;
			justify-content: center;
			align-items: center;
			text-align: center;
			// background-color: #1c2752;
		}
	}
}
