@mixin title {
	font-family: Mulish, sans-serif;
	font-size: clamp(1.875rem, 1.545rem + 1.65vw, 2.813rem);
	font-weight: 400;
	letter-spacing: 0.36000001430511475px;
	text-align: left;
	color: #1c2752;
	margin: clamp(0.625rem, 0.361rem + 1.32vw, 1.375rem) 0 clamp(0.438rem, 0.262rem + 0.88vw, 0.938rem) 0;
}

@mixin title-above {
	font-family: Inter, sans-serif;
	// font-size: 18px;
	font-size: clamp(0.813rem, 0.703rem + 0.55vw, 1.125rem);
	font-weight: 400;
	line-height: clamp(0.938rem, 0.674rem + 1.32vw, 1.688rem);
	letter-spacing: 0.36000001430511475px;
	text-align: left;
	color: #1c2752;
}

// 	@include title-above;

@mixin title-above-icon {
	font-size: clamp(1.125rem, 0.861rem + 1.32vw, 1.875rem);
	color: #ffb342;
	font-weight: 600;
	// padding-left: 20px;
}

// @include  title-above-icon

@mixin descript {
	font-family: Inter, sans-serif;
	font-size: clamp(0.813rem, 0.703rem + 0.55vw, 1.125rem);
	font-weight: 400;
	line-height: 27px;
	letter-spacing: 0.36000001430511475px;
	text-align: left;
	color: #7e7e7e;
}
