.customer {
	display: flex;
	justify-content: space-between;
	padding: clamp(4.375rem, 2.901rem + 7.37vw, 8.75rem) 0;
	// margin-bottom: 00px;
	min-height: 100vh;

	&__blocks {
		gap: clamp(1.563rem, 1.141rem + 2.11vw, 2.813rem);
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		// margin-bottom: 90px;
		@media (max-width: 700px) {
			grid-template-columns: repeat(1, 1fr);
			margin-bottom: 20px;
		}
	}

	&__above {
		display: flex;
		align-items: center;
		gap: 10px;
		background-color: #f4f4f4;
		max-width: 170px;
		text-align: center;
		justify-content: center;
		margin-bottom: 22px;
		i {
			color: #ffb342;
			@include title-above-icon;
		}

		p {
			color: #1c2752;
			@include title-above;
		}
	}

	&__title {
		@include title;
		margin-bottom: 40px;
	}
}

.says {
	height: 500px;
	margin-bottom: 200px;
}

//feedback

.feedback {
	border: 1px solid #ddd;
	margin-bottom: 40px;
	&__above {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__blocks {
		border: 1px solid #ddd;
		padding: 20px 40px;
		@media (max-width: 800px) {
			padding: 20px clamp(1.25rem, 0.829rem + 2.11vw, 2.5rem);
		}
	}

	.author {
		display: flex;
		align-items: center;
		margin-bottom: 20px;
		&__photo {
			width: auto;
			background-color: #1c275231;
			border-radius: 100%;
			margin-right: 10px;
			img {
				max-width: 60px;
				height: 60px;
				padding: 0;
			}
			.avatar {
				display: none;
				&.active {
					display: block;
				}
			}
		}

		&__initials {
			.name {
				font-size: clamp(0.938rem, 0.79rem + 0.74vw, 1.375rem);
				font-family: "Mulish", sans-serif;
				color: #1c2752;
			}

			.city {
				font-size: clamp(0.813rem, 0.707rem + 0.53vw, 1.125rem);
				color: #7e7e7e;
				font-family: "Inter", sans-serif;
			}
		}

		&__initial {
			display: none;
			&.active {
				display: block;
			}
		}
	}

	.quotes {
		background-color: #ffb342;
		display: flex;
		justify-content: center;
		align-items: center;
		width: clamp(2.5rem, 2.184rem + 1.58vw, 3.438rem);
		height: clamp(2.5rem, 2.184rem + 1.58vw, 3.438rem);
		border-radius: 100px 0px 0px 0px;
		border-radius: 100%;
		font-size: clamp(0.938rem, 0.727rem + 1.05vw, 1.563rem);
		cursor: pointer;
	}

	// line

	&__line {
		width: 100%;
		background-color: #dcdcdc;
		height: 1px;
	}

	//messages

	&__message {
		position: absolute; // Робимо елемент абсолютним, щоб не займав простір
		margin-top: 20px;
		opacity: 0;
		z-index: 1;
		visibility: hidden;
		// margin-bottom: 50px;
		min-height: 100px;
		// background-color: #1c2752;
		@media (max-width: 800px) {
			line-height: clamp(1.25rem, 1.096rem + 0.77vw, 1.688rem);
		}

		&.active {
			opacity: 1;
			visibility: visible;
			position: relative;
		}
		p {
			@include descript;
			font-size: clamp(0.813rem, 0.707rem + 0.53vw, 1.125rem);
			font-weight: 700;
		}

		span {
			color: #1c2752;
		}
	}

	// buttons

	&__btns {
		display: flex;
		align-items: center;
		gap: 20px;
		margin-top: 20px;
		margin-bottom: 20px;
		cursor: pointer;
		z-index: 999;
		.btn {
			// padding: 25px;
			padding: 15px;
			background: #f4f4f4;
			color: #1c2752;
			font-size: 20px;
			display: flex;
			text-align: center;
			justify-content: center;
			align-items: center;
			border-radius: 100%;
			cursor: pointer;
		}
	}
}

// percent

.percent {
	background-color: #ffb342;
	// height: auto;

	& &__block {
		padding: 20px 40px;
		h5 {
			color: #1c2752;
			// @include title;
			margin-bottom: 15px;
			font-family: Mulish, sans-serif;
			font-size: clamp(0.938rem, 0.727rem + 1.05vw, 1.563rem);
		}

		.descript {
			margin-bottom: 15px;
			@include descript;
			color: #1c2752;
		}
	}

	&__number {
		display: flex;
		align-items: center;
		gap: 10px;
		font-family: Mulish, sans-serif;
		font-size: clamp(0.938rem, 0.727rem + 1.05vw, 1.563rem);
		color: white;

		i {
			position: relative;
			color: rgba(0, 0, 0, 0.682);
			background-color: white;
			// padding: 2px 10px;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 50px;
			height: 50px;
			border-radius: 100%;
		}
	}
}

// second block
// Form

.form {
	width: 100%;
	height: 100%;
	@media (max-width: 700px) {
		margin-top: 50px;
	}
	@media (max-width: 450px) {
		margin-top: 100px;
	}
	&__block {
		background-color: #f4f4f4;
		padding: 60px clamp(1.25rem, 0.829rem + 2.11vw, 2.5rem) 65px;
	}

	input:not(:last-child) {
		padding: clamp(0.813rem, 0.699rem + 0.57vw, 1.125rem) 0 clamp(0.813rem, 0.699rem + 0.57vw, 1.125rem) 40px;
		border: 1px solid #cccccc;
		width: 100%;
		background-color: white;
	}

	.submit {
		max-width: 484px;
		width: 100%;
		height: clamp(3.438rem, 3.142rem + 1.48vw, 4.25rem);
		border: 2px 0px 0px 0px;
		opacity: 0px;
		border: 2px solid #ffb342;
	}

	textarea {
		width: 100%;
		padding: 20px 0 0 40px;
		height: 120px;
		border: 1px solid #cccccc;
		width: 100%;
		background-color: white;
	}

	.submit {
		font-family: Inter, sans-serif;
		font-size: 17px;
		font-weight: 700;
		line-height: 27px;
		letter-spacing: 0.36000001430511475px;
		text-align: center;
		background-color: #ffb342;
	}
}

// clients

.clients {
	position: relative;
	// padding-top: 100px;
	margin-top: 150px;
	@media (max-width: 700px) {
		margin-top: 50px;
		padding-top: 0px;
	}
	&__title {
		color: #1c2752;
	}
}

.company {
	gap: 50px;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	justify-content: center;
	align-items: center;
	justify-items: center;
	max-width: max-content;
	margin: 0 auto;
	margin-top: 40px;
	@media (max-width: 700px) {
		grid-template-columns: repeat(2, 1fr);
	}
}
